
.totalTrip {
    border-radius: 5%;
    width: 50%;
    background: #f5f5f5;
    /* border: solid 1px #c1c1c1; */
    margin: 2%;
    box-shadow: beige 0px 0px;
}

.tripInfo {
    display: inline-flex;
    width: 100%;
}

.Card-title h2 {
    vertical-align: middle;
    margin: auto;
    top: 50%;
    line-height: 80px;
    text-align: center;
    height: 100%;
    font-size: 1.5em;
    color: var(--danger);
}

.price {
    font-size: 1.5em;
    color: var(--danger);
    font-weight: 700;
}

.Card-style {
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5) !important;
    margin-top: 5% !important;
}

.card-header {
    background-color: white !important;
}

.a-phone {
    line-height: 55px;
}

.phone-icon {
    font-size: 2.5em;
    /* border: solid 1px var(--danger);
    border-radius: 50%; */
    padding: 2%;
    color: var(--danger);
    height: 40%;
}

.car-icon {
    font-size: 1.5em;
    /* border: solid 1px var(--danger); */
    /* border-radius: 50%; */
    padding: 2%;
    color: var(--danger);
    margin: 1%;
}

.icon-car_taked {
    color: var(--yellow) !important;
    font-size:  1.5em;
    padding:  2%;
    margin: 1%;
}

.reverse {
    display: inline-flex;
    flex-direction: column-reverse;
}

.trip {
    display: flex;
}

.tripCancel {
    justify-content: flex-end;
}

.action {
    display: flex;
    justify-content: flex-end;
}

.text-taked {
    color: var(--green);
}

.text-taked svg {
    font-size: 1.7em;
}

.Card-title h2:nth-child(2) {
    display: none;
}

.done {
    color: var(--green);
    font-size: 2em;
}

.check {
    line-height: 55px;
    cursor: pointer;
}

.trans-icons {
    padding-right: 10px !important;
    padding-left: 10px !important;
}

@media screen and (max-width: 600px) {
    .trans-icons {
        display: inline-flex;
        justify-content: space-between;
    }
    .phone-icon {
        height: auto !important;
    }
    .trans-icons {
        padding-left: 15px !important;
        padding-right: 15px !important;
    }
}