.body {
    background-color: #fafafa;
}

.container {
    padding: 15px;
}

.icons {
    font-size: 2em !important;
    color: var(--red);
}

.f-icons col {
    text-align: center;
}

.fleets {
    overflow: auto; /* Overlay y scroll  works too */
}

.ourFleets {
    text-transform: uppercase;
}

.front-img {
    padding-top: 3rem;
}

h6 {
    margin-bottom: 1.5rem;
}

.h70 {
    /*Change from 70 to 55*/
    height: 55vh;
}

.card {
    /* -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75); */
    border: none !important;
    padding-top: 2rem;
}

.card-title {
    font-size: 1.1em !important;
    color: var(--red);
}
.ct-ml {
    margin-left: 0 !important;
}

.card-fleet {
    width: 18rem;
    margin: 1rem;
    overflow: hidden;
}

/* .card-fleet img {
    width: 145vw;
} */
.card-fleet svg {
    vertical-align: bottom !important;
}

.fleets-img {
    display: inline-flex;
    scroll-behavior: smooth;
    overflow-x: scroll;
}

.card-text {
    color: black !important;
    font-weight: 600;
}

.car-class_type {
    position: relative;
    top: calc(100% - 100px);
}
.passenger {
    color: black;
    position: relative;
    top: -5vh;
}
.frontHeading {
    text-align: center;
    top: 15vh;
}

/*Card*/
.card-img {
    margin: 2rem 0;
}

.btns {
    display: inline-flex;
}


@media screen and (max-width: 600px) {
    .front-img {
        display: none;
    }
}

@media screen and (min-width: 992px) {
    .card-fleet img {
        width: 17vw;
    }
}