.footerContainer {
    padding: 15px;
    background-color: var(--red);
}

h5 {
    text-align: center;
    color: var(--title-snow);
}

ul {
    list-style: none;
}

ul li a {
    color: snow;
}

ul li a:hover {
    color: var(--linkHover);
}

.social {
    color: snow;
}

.social span {
    padding: 0 0 0 .75rem;
    vertical-align: middle;
}