.destination {
    justify-content: space-around;
}

.map {
    width: 85vw;
    margin: auto;
    /* padding: 15px; */
}

.dist-time {
    display: flex;
    justify-content: space-around;
    background-color: var(--red);
    color: snow;
    font-size: 1.5rem;
    font-weight: 600;
    text-transform: uppercase;
}

.dist-time col {
    font-size: large;
    font-weight: 700;
    text-align: center;
}

.dateTime:nth-child(2) {
    text-align: center;
    margin: 15px auto;
}

.dateTime Col {
    text-align: center;
}

.card-title {
    margin-left: -75%;
}

.car-class_type {
    position: relative;
    top: calc(100% - 70px);
}

.plus-less {
    flex-grow: .2 !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
}

.plus-less button svg {
    font-size: 2rem;
    color: var(--red);
}

.kid-chair {
    margin-right: 1rem;
}

.btn-submit {
    text-align: center !important;
    margin-bottom: 5rem;
}

.price {
    font-size: 2.5rem;
    font-weight: 600;
    color: var(--red);
}

.modal-title {
    color: var(--red);
}

.css-1aty463-TopBar {
    color: var(--danger) !important;
}
.css-1jj6dwi-ClockHand {
    fill: var(--danger) !important;
}
.css-4456cn-ClockHand {
    stroke: var(--danger) !important;
}
.react-timekeeper__meridiem--active {
    background: var(--danger) !important;
    color: snow !important;
}
/* .css-xbtxlx-Meridiems{ 
    background: var(--danger) !important;
} */
.css-16j6ljo-ClockHand {
    fill: var(--danger) !important;
} 

@media screen and (width: 1200px) {
    .adults {
        width: 30% !important;
    }
}